const url = "https://api.dictionaryapi.dev/api/v2/entries/en/";
// const text = document.getElementById("text");
// const wordsound = document.getElementById("word-sound");
// const btn = document.getElementById("button");
const searchtext = document.getElementById("search-engine");
const btn = document.getElementById("search-button");
const words = document.getElementById("word-sound");
const audio = document.getElementById("sound");

btn.addEventListener("click", () => {
  fetch(`${url}${searchtext.value}`)
    .then((data) => data.json())

    .then((res) => {
      //console.log(`${res[0].meanings[0].definitions[0].definition}`);

      words.innerHTML = ` 
      <div class="word">
      <h1>${searchtext.value}</h1>
  

     
    </div>
    <div class="details">
      <p>${res[0].meanings[0].partOfSpeech}</p>
      <p>/${res[0].phonetic}</p>
    </div>
    <div id="word-meaning"> ${
      res[0].meanings[0].definitions[0].definition
    }.</div>
    <div id="word-example"> ${
      res[0].meanings[0].definitions[0].example || ""
    }</div>`;
      audio.setAttribute("src", `https:${res[0].phonetics[0].audio}`);
    })
    .catch((e) => {
      words.innerHTML = `<div id="error-message>${e.meanings}</div>`;
    });
  function soundUp() {
    audio.play();
  }
});
